@import url(app.css);

footer {
  background: linear-gradient(
    135deg,
    var(--text-primary) 0%,
    var(--gray-800) 100%
  );
  color: var(--white);
  margin-top: 4rem;
}

.footer {
  display: flex;
  width: 100%;
  max-width: 1200px;
  align-items: flex-start;
  justify-content: space-between;
  margin: 0 auto;
  padding: 4rem 2rem 2rem;
  gap: 3rem;
}

.footer h3 {
  color: var(--white);
  margin-bottom: 1.5rem;
  font-size: 1.25rem;
  font-weight: 700;
  font-family: var(--font-heading);
  position: relative;
}

.footer h3::after {
  content: "";
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  width: 50px;
  height: 3px;
  background: var(--bg-primary);
  border-radius: var(--radius-sm);
}

.footer a {
  color: var(--gray-300);
  transition: all var(--transition-normal);
  position: relative;
}

.footer a:hover {
  color: var(--white);
  transform: translateX(5px);
}

.footer-links ul {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.social ul {
  display: flex;
  gap: 1rem;
}

.social li {
  border-radius: var(--radius-full);
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.social li::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--bg-primary);
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.social li:hover::before {
  opacity: 0.2;
}

.social li:hover {
  transform: translateY(-3px) scale(1.1);
}

.social a {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 2;
}

.social svg {
  font-size: 1.5rem;
  transition: all var(--transition-normal);
}

.social li:hover svg {
  transform: scale(1.1);
}

.facebook {
  background: linear-gradient(135deg, #1877f2, #42a5f5);
}

.instagram {
  background: linear-gradient(
    135deg,
    #667eea 0%,
    #764ba2 25%,
    #f093fb 50%,
    #f5576c 75%,
    #4facfe 100%
  );
}

.youtube {
  background: linear-gradient(135deg, #ff0000, #ff4444);
}

.footer-bottom {
  text-align: center;
  background: var(--gray-900);
  color: var(--gray-400);
  padding: 1.5rem 0;
  border-top: 1px solid var(--gray-800);
}

.footer-bottom a {
  color: var(--primary-color);
  font-weight: 600;
  transition: color var(--transition-fast);
}

.footer-bottom a:hover {
  color: var(--primary-light);
}

@media screen and (max-width: 768px) {
  .footer {
    flex-direction: column;
    text-align: center;
    padding: 3rem 1rem 1rem;
    gap: 2rem;
  }

  .footer h3::after {
    left: 50%;
    transform: translateX(-50%);
  }

  .social ul {
    justify-content: center;
  }
}

@media screen and (max-width: 480px) {
  .footer {
    padding: 2rem 1rem 1rem;
  }

  .social ul {
    gap: 0.75rem;
  }

  .social li {
    width: 45px;
    height: 45px;
  }

  .social svg {
    font-size: 1.25rem;
  }
}
